.productList {
  box-sizing: border-box;
  font-size: 20vh;
  font-weight: 700;
  width: 100%;
  padding: 15px;
  min-height: 200px;
  line-height: 1;
  color: #fffdfd;
  text-align: left;
  transition: all 0.6s ease-in-out;
}
.productList > div:first-child {
  position: relative;
  width: 100%;
  height: 100vh;
  flex-grow: 1;
  z-index: 99;
}

.productList .full::after {
  position: absolute;
  content: "";
  width: 15vh;
  height: 15vh;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: #f50000;
  z-index: -1;
}
.productList span {
  margin: 0;
  position: relative;
  color: #f50000;
  margin-left: 1rem;
}
.arrow {
  display: block;
  position: relative;
  top: 10vh;
  left: 65vw;
}

.list {
  overflow: hidden;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%;
  list-style-type: none;
  text-decoration: none;
  color: #f50000;
}

.project {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 50%;
  min-width: 300px;
  height: 80vh;
  padding: 1rem;
}
.project a {
  height: 100%;
  color: #f50000;
  font-size: x-large;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.foto {
  position: relative;
  height: 90%;
  width: 75%;
  overflow: hidden;
  background-color: #09359a;
  box-shadow: 3rem 5rem 0 #f50000;
}
.foto img {
  position: relative;
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: grayscale(1);
  position: relative;
  transition: all 0.5s ease-in-out;
  z-index: 0;
}

.foto::before {
  content: " ";
  display: block;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: #f50000;
  z-index: 5;
  opacity: 0.3;
}
.foto:hover::before {
  transition: all 0.5s ease-in-out;
  opacity: 0;
}

.foto:hover img {
  filter: grayscale(0);
}

.fotoName {
  color: #09359a;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.8rem;
  line-height: 3rem;
  z-index: 103;
}
@media screen and (max-width: 900px) {
  .productList {
    margin: 0;
    padding: 0;
  }

  .productList span {
    margin: 0;
    position: relative;
    color: #f50000;
    margin-left: 1rem;
  }
  .arrow {
    display: block;
    position: relative;
    top: 10vh;
    left: 65vw;
  }

  .list {
    overflow: hidden;
    margin: 0;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    list-style-type: none;
    text-decoration: none;
    color: #f50000;
  }

  .project {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    width: 100%;
    min-width: 300px;
    height: 80vh;
    padding: 1rem;
    margin-bottom: 5vh;
  }
  .project a {
    height: 100%;
    color: #f50000;
    font-size: x-large;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  .foto {
    height: 90%;
    width: 95%;
  }
  .foto img {
    position: relative;
    width: 100%;
    height: 100%;
  }

  .foto::before {
    content: " ";
    display: block;
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: #f50000;
    z-index: 5;
    opacity: 0.3;
  }
  .foto::before:nth-of-type(2n) {
    background: #09359a;
  }

  .fotoName {
    color: #09359a;
    text-transform: uppercase;
    text-align: right;
    padding-right: 3rem;
    font-size: 5vw;
    line-height: 3rem;
    z-index: 103;
  }
}
