@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,600;0,700;0,900;1,400&family=Source+Sans+Pro:ital,wght@0,300;0,400;0,700;0,900;1,400&display=swap");
* {
  margin: 0;
  padding: 0;
}
/* font-family: 'Montserrat', sans-serif;
font-family: 'Source Sans Pro', sans-serif; */

.App {
  margin: auto;
  text-align: center;
  display: flex;
  font-family: "Montserrat", sans-serif;
  color: #cbd2d0;
  height: 100vh;
  max-width: 1640px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #cbd2d0;
}

.blog {
  display: block;
  flex-grow: 2;
  height: 100%;
  min-height: fit-content;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.blog::-webkit-scrollbar {
  width: 0;
  height: 0;
}
@media screen and (max-width: 900px) {
  .App {
    margin: auto;
    text-align: center;
    flex-direction: row;
    color: #cbd2d0;
  }
}
