.Header {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 122px;
  height: 100%;
  flex-wrap: nowrap;
}

.null_logo {
  position: fixed;
  top: 0px;
  background-color: #f50000;
  width: 122px;
  height: 122px;
}
.logo_sygnet {
  position: fixed;
  top: 0px;
  width: auto;
  height: 122px;
  background-color: #09359a;
  display: flex;
  text-align: center;
  justify-content: center;
}
.social {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 10rem;
  justify-self: flex-end;
  font-size: 1.2rem;
  font-weight: 900;
  color: #fffdfd;
  transition: ease-in-out 0.2s;
}
.social span {
  display: flex;
  position: absolute;
  transform: translate(0px, 20px) rotate(-0.25turn);
  transition: ease-in-out 0.2s;
}

.insta {
  background-color: #f50000;
}

.insta:hover {
  background-color: #fffdfd;
}
.insta:hover span {
  color: #f50000;
  transform: translate(0px, 4rem) rotate(-0.25turn);
}

.fb {
  background-color: #09359a;
}

.fb:hover {
  background-color: #fffdfd;
}
.fb:hover span {
  color: #09359a;
  transform: translate(0px, 4rem) rotate(-0.25turn);
}

@media screen and (max-width: 900px) {
  .Header {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 40px;
    height: 100%;
    flex-wrap: nowrap;
  }
  .logo_sygnet {
    position: fixed;
    top: 0px;
    width: 40px;
    height: 40px;
    background-color: #09359a;
    display: flex;
    text-align: center;
    justify-content: center;
  }
}
